html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Red Hat Display", sans-serif;
}

p {
  font-size: 16px;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* 
Example: 
h1 {
  @include breakpoint(large) {
    font-size: 3rem;
  }
}

Output:
@media (min-width: 87.5em) {
  h1 {
    font-size: 3rem;
  }
} */
/* 
Example:
h1 {
  font-size: rem(30);
}

Output:
h1 {
  font-size: 1.875rem;
} */
.flex {
  display: flex;
}
.flex-jc-c {
  justify-content: center;
}
.flex-jc-s {
  justify-content: flex-start;
}
.flex-jc-e {
  justify-content: flex-end;
}
.flex-ai-c {
  align-items: center;
}

.grid-col-3 {
  display: grid;
  grid-template-columns: auto auto auto;
}

.text-white {
  color: white;
}
.text-pale-blue {
  color: #e0e8ff;
}
.text-bright-blue {
  color: #3829e0;
}
.text-very-pale-blue {
  color: #f5f7ff;
}
.text-desaturated-blue {
  color: #7280a7;
}
.text-dark-blue {
  color: #1f2f56;
}
.text-bold {
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.card {
  width: 28.125rem;
  height: 43.75rem;
  background-color: white;
  border-radius: 25px;
}
.card-body {
  padding: 3.125rem;
}
.card img {
  border-radius: 25px 25px 0px 0px;
  width: 100%;
}
@media (max-width: 39.9375em) {
  .card {
    width: 20.625rem;
    height: 35.625rem;
  }
  .card .card-body {
    padding: 30px;
  }
}

main {
  width: auto;
  height: 100vh;
  background-color: #e0e8ff;
  background-image: url(../../../images/pattern-background-desktop.svg);
  background-repeat: no-repeat;
  background-size: 100vw;
}
@media (max-width: 39.9375em) {
  main {
    background-image: url(../../../images/pattern-background-mobile.svg);
  }
}

.plan {
  background-color: #f5f7ff;
  border-radius: 20px;
  padding: 25px;
}
.plan img {
  width: auto;
  height: auto;
  padding-right: 15px;
}
.plan p {
  margin: 0px;
}
.plan a {
  margin-left: 5rem;
}
@media (max-width: 39.9375em) {
  .plan {
    padding: 10px 5px;
    border-radius: 10px;
  }
  .plan a {
    margin: 0px;
    padding-left: 40px;
  }
}

.btn {
  margin-top: 30px;
  padding: 15px;
  width: 100%;
  border-radius: 10px;
  border: none;
}
.btn-blue {
  background-color: #3829e0;
  font-weight: bold;
  color: white;
  font-family: "Red Hat Display", sans-serif;
}
.btn-blue:hover {
  background-color: #766cea;
}
.btn-link {
  color: #7280a7;
  background: none;
  font-weight: bold;
  font-family: "Red Hat Display", sans-serif;
}
.btn-link:hover {
  color: #45506e;
}

.link {
  color: #3829e0;
  text-decoration: underline;
  font-weight: bold;
}
.link:hover {
  text-decoration: none;
  color: #766cea;
}

.shadow {
  box-shadow: 0px 20px 20px #dedbfa;
}